import { useEffect, useState } from 'react';
import { supabase } from '../lib/supabaseClient';

export default function Websites() {
  const [sites, setSites] = useState([]);

  useEffect(() => {
    const fetchSites = async () => {
      const { data, error } = await supabase.from('web_sites').select('*');
      if (!error) setSites(data);
    };
    fetchSites();
  }, []);

  return (
    <div className="p-6">
      <h1 className="text-xl font-bold mb-4">Websites List</h1>
      <ul className="space-y-2">
        {sites.map(site => (
          <li key={site.id} className="p-4 bg-gray-100 rounded shadow">
            <p><strong>Link:</strong> {site.link}</p>
            <p><strong>Badge:</strong> {site.badge}</p>
          </li>
        ))}
      </ul>
    </div>
  );
}
